.App {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 600;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 100px;
  width: 420px;
  height: 60vh;
  box-shadow: 0px 0px 3px 0px lightgrey;
  border-radius: 12px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  max-width: 100%;
  color: #555555;
  font-weight: 600;
}

:root {
  --doc-height: 100%;
}

.button {
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  color: #555555;
}

.button:hover {
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.05);
}

#options {
  position: fixed;
  box-shadow: 0px 0px 3px 0px lightgrey;
  margin-bottom: 100%;
  border-radius: 8px;
  background-color: white;
  padding: 4px;
  z-index: 10;
}

.todo {
  padding: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
}

.todolist {
  border-radius: 10px;
  cursor: pointer;
  padding: 4px 16px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  display: "flex";
}

.todolist-container {
  height: 100%;
  max-height: 100%;
  overflow-y: auto;
}

.todolist-title {
  outline: none;
  border-radius: 8px;
  padding: 8px;
}

.todolist-length {
  height: 20px;
  width: 20px;
  background-color: lightgrey;
  opacity: 0.6;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 600;
  margin-left: 4px;
}

.todolist:hover {
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.03);
}

.todo:hover {
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.03);
}

.minus-icon {
  height: 40px;
  width: 40px;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.minus-icon:hover {
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.05);
}

@media only screen and (max-width: 500px) {
  .App {
    border-radius: 0px;
    top: 0;
    height: var(--doc-height);
    width: 100%;
  }
}
